.firstQ {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: wheat;

  h3 {
    font-size: 30px;
    margin: 20px
  }

  .form {
    display: flex;
    align-items: center;
    flex-direction: column;

    label {
      width: 140px;

      margin: 10px;

      font-size: 20px;

      display: flex;
      justify-content: space-between;
      align-items: center;

      cursor: pointer;
    }

  }

  .button {
    width: 150px;
    margin-top: 50px;
    padding: 10px;
    color: black;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
  }

  .disabledBtn {
    background-color: gray;
    opacity: 0.5;
  }
}